import { mapActions } from 'vuex'

export default {
  name: 'Store',
  props: {
    book: {
      type: Object,
      default() {
        return {
          bookId: 0,
        }
      },
    },
  },
  computed: {
    isCollection() {
      return this.$store.getters.isCollection(this.book.bookId)
    },
    note() {
      return this.isCollection ? '取消' : '收藏'
    },
  },
  methods: {
    ...mapActions(['handleAddCollection', 'handleDelCollection']),
  },
}
